<template>
  <div class="vtl">
    <div v-if="model.name !== 'root'" :id="model.id" class="vtl-node" :class="'vtl-tree-node'">
      <div
        class="vtl-border vtl-up"
        :class="{ 'vtl-active': isDragEnter }"
        @drop="onDrop"
        @dragenter="dragEnter"
        @dragover="dragOver"
        @dragleave="dragLeave"
      />
      <div
        :class="treeNodeClass"
        draggable="true"
        @dragstart="dragStart"
        @dragend="dragEnd"
        @mouseover="mouseOver"
        @mouseout="mouseOut"
        @click.stop="click"
      >
        <span class="vtl-caret vtl-is-small" v-if="model.children && model.children.length > 0">
          <i class="vtl-icon" :class="caretClass" @click.prevent.stop="toggle"></i>
        </span>
        <div class="vtl-node-content">
          <slot name="leafNameDisplay" :expanded="expanded" :model="model" :root="rootNode">
            {{ model.name }}
          </slot>
        </div>
        <div class="vtl-operation" v-show="isHover">
          <span
            :title="addNodeTitle"
            @click.stop.prevent="addChild(false)"
            v-if="!model.addTreeNodeDisabled"
          >
            <slot name="addTreeNodeIcon" :expanded="expanded" :model="model" :root="rootNode">
              <i class="vtl-icon vtl-icon-folder-plus-e"></i>
            </slot>
          </span>
          <span :title="editTitle" @click.stop.prevent="editNode" v-if="!model.editNodeDisabled">
            <slot name="editNodeIcon" :expanded="expanded" :model="model" :root="rootNode">
              <i class="vtl-icon vtl-icon-edit"></i>
            </slot>
          </span>
          <span :title="deleteTitle" @click.stop.prevent="delNode" v-if="!model.delNodeDisabled">
            <slot name="delNodeIcon" :expanded="expanded" :model="model" :root="rootNode">
              <i class="vtl-icon vtl-icon-trash"></i>
            </slot>
          </span>
        </div>
      </div>
    </div>

    <!-- children -->
    <div
      :class="{ 'vtl-tree-margin': model.name !== 'root' }"
      v-show="model.name === 'root' || expanded"
      v-if="isFolder"
    >
      <item
        v-for="model in model.children"
        :default-tree-node-name="defaultTreeNodeName"
        :default-expanded="defaultExpanded"
        :model="model"
        :key="model.id"
        :selecting-node-id="selectingNodeId"
      >
        <template v-slot:leafNameDisplay="slotProps">
          <slot name="leafNameDisplay" v-bind="slotProps" />
        </template>
        <template v-slot:addTreeNodeIcon="slotProps">
          <slot name="addTreeNodeIcon" v-bind="slotProps" />
        </template>
        <template v-slot:editNodeIcon="slotProps">
          <slot name="editNodeIcon" v-bind="slotProps" />
        </template>
        <template v-slot:delNodeIcon="slotProps">
          <slot name="delNodeIcon" v-bind="slotProps" />
        </template>
      </item>
    </div>
  </div>
</template>

<script>
import { TreeNode } from './Tree.js'
import { removeHandler } from './tools.js'

let compInOperation = null

export default {
  name: 'vue-tree-list',

  props: {
    model: {
      type: Object
    },
    selectingNodeId: {
      type: Number
    },
    defaultTreeNodeName: {
      type: String,
      default: 'Tree Node'
    },
    addNodeTitle: {
      type: String,
      default: ''
    },
    editTitle: {
      type: String,
      default: ''
    },
    deleteTitle: {
      type: String,
      default: ''
    },
    defaultExpanded: {
      type: Boolean,
      default: true
    }
  },

  data: function() {
    return {
      isHover: false,
      isDragEnter: false,
      expanded: this.defaultExpanded
    }
  },

  computed: {
    rootNode() {
      var node = this.$parent
      while (node._props.model.name !== 'root') {
        node = node.$parent
      }
      return node
    },
    caretClass() {
      return this.expanded ? 'vtl-icon-caret-down' : 'vtl-icon-caret-right'
    },
    treeNodeClass() {
      const {
        model: { id }
      } = this

      return {
        'vtl-node-main': true,
        'vtl-selecting': this.selectingNodeId === id
      }
    },
    isFolder() {
      return this.model.children && this.model.children.length
    }
  },

  beforeCreate() {
    this.$options.components.item = require('./VueTreeList').default
  },

  beforeDestroy() {
    removeHandler(window, 'keyup')
  },

  methods: {
    toggle() {
      if (this.isFolder) this.expanded = !this.expanded
    },
    mouseOver() {
      this.isHover = true
    },
    mouseOut() {
      this.isHover = false
    },
    click() {
      if (this.model.children && this.model.children.length) {
        this.toggle()
        return
      }
      this.rootNode.$emit('click', {
        toggle: this.toggle,
        ...this.model
      })
    },
    addChild() {
      const name = this.defaultTreeNodeName
      this.expanded = true
      var node = new TreeNode({ name, isLeaf: false })
      this.model.addChildren(node, true)
      this.rootNode.$emit('add-node', node)
    },
    editNode() {
      this.rootNode.$emit('edit', { id: this.model.id, oldName: this.model.name })
    },
    delNode() {
      this.rootNode.$emit('delete-node', this.model)
    },
    // drag and drop
    onDrop() {
      if (!compInOperation) return
      if (compInOperation.model.parent.id !== this.model.parent.id) {
        this.rootNode.$emit('drop-diff-level')
        this.isDragEnter = false
        return
      }
      compInOperation.model.insertBefore(this.model)
      this.isDragEnter = false
      this.rootNode.$emit('drop')
    },
    dragEnter() {
      if (!compInOperation) return
      this.isDragEnter = true
    },
    dragOver(e) {
      e.preventDefault()
    },
    dragLeave() {
      if (!compInOperation) return
      this.isDragEnter = false
    },
    dragStart(e) {
      compInOperation = this
      // for firefox
      e.dataTransfer.setData('data', 'data')
      e.dataTransfer.effectAllowed = 'move'
    },
    dragEnd() {
      compInOperation = null
    }
  }
}
</script>

